import React from "react"

import '../styles/components/CardProgram.scss'

import { teachersData } from "../data/teatchers"

import lesson_img from '../styles/img/lesson.png'
import teacher_img from '../styles/img/teacher.png'

const CardProgram = () => {
    return (
        <div className={'card_program'}>
            <h2>Программа курса</h2>
            <div className={'card_program_wrapper'}>
                {teachersData.map(teacher => (
                    <div className={'teacher_card'}>
                        <div className={'teacher_card_top'}>
                            <img src={lesson_img} alt={teacher.lesson}/>
                            <ul>
                                {teacher.course.map(item => (
                                    <li>- {item}</li>
                                ))}
                            </ul>
                        </div>
                        <div className={'teacher_card_bottom'}>
                            <div className={'teacher_card_bottom_image'}>
                                <img src={teacher_img} alt={teacher.name}/>
                                <span>Инстаграмм</span>
                            </div>
                            <p>{teacher.description}</p>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    )
}

export default CardProgram