import React from "react"

import '../styles/components/CardSummary.scss'

const CardSummary = () => {
    return (
        <div className={'card_summary'}>
            <h2>Результат</h2>
            <p>
                Этот курс поможет вам поверить в себя, полюбить своё отражение
                начать двигаться к своей цели и, не смотря ни на что, улыбаться!
            </p>
        </div>
    )
}

export default CardSummary