import React from "react"

import price from '../styles/img/price-label.png'

import '../styles/components/CardPrice.scss'

const CardPrice= () => {
    return (
        <div className={'card_price'}>
            <h2>Стоимость курса</h2>
            <img src={price} alt={'price'}/>
            <h3>Специальное предложение</h3>
            <button className={'btn'}>Приобрести курс</button>
            <p>После оплаты на ваш E-mail придёт ообщение с доступом к урокам</p>
        </div>
    )
}

export default CardPrice