import React, { useState } from "react"

import '../styles/components/CardHeader.scss'
import facebook from "../styles/img/facebook.svg"
import instagram from "../styles/img/instagram.svg"

const CardHeader = () => {
    const [isOpenNav, setIsOpenNav] = useState(false);

    const closeNavMenu = () => {
        setIsOpenNav(false)
    }
    const openNavMenu = () => {
        setIsOpenNav(true)
    }

    return (
        <div className={'card_header'}>
            <i className={'card_header_decor card_header_decor_top'}/>
            <i className={'card_header_menu'} onClick={openNavMenu}/>
            <h1>Lady school</h1>
            <span>женский онлай курс</span>
            <i className={'card_header_decor card_header_decor_bottom'}/>
            {isOpenNav && (
                <div className={'nav'}>
                    <i className={'nav_close'} onClick={closeNavMenu}/>
                    <ul>
                        <li><a href={'#about'}>О курсе</a></li>
                        <li><a href={'#program'}>Программа</a></li>
                        <li><a href={'#price'}>Стоимость</a></li>
                        <li><a href={'#start'}>Инструкция</a></li>
                        <li><a href={'#footer'}>Контакты</a></li>
                    </ul>
                    <div className={'divider divider_horizontal'}/>
                    <p>Узнайте о нас больше</p>
                    <div className={'footer_social'}>
                        <a href={'/'} target={'__blank'}><img src={facebook} alt={'facebook'}/></a>
                        <a href={'/'} target={'__blank'}><img src={instagram} alt={'instagram'}/></a>
                    </div>
                </div>
            )}
        </div>
    )
}

export default CardHeader
